import sun from "../images/sun.png"; // 
import mon from "../images/mon.png"; // 
import tue from "../images/tue.png"; // 
import wed from "../images/wed.png"; // 
import thu from "../images/thu.png"; // 
import fri from "../images/fri.png"; // 
import sat from "../images/sat.png"; // 

import "./Today.css";

const days_list = [sun, mon, tue, wed, thu, fri, sat];
const dname_list = ["", "", "", "", "", "", ""];
const dqutoes_list = [
  ",     .",
  "!  ,    .",
  "   .      .",
  "  !    .",
  "   !       .",
  " !   .",
  "     .",
];

const day_emo = days_list[new Date().getDay()];
const day_name = dname_list[new Date().getDay()];
const day_quote = dqutoes_list[new Date().getDay()];
function Today() {
  return (
    <div className="today">
      <img className="today-emoji" alt="today emoji" src={day_emo}></img>
      <h1 className="title">{day_name}!</h1>
      <h3 className="subtitle">{day_quote}</h3>
    </div>
  );
}

export default Today;
